import { WorldwideService } from './../worldwide/worldwide.service';
import { Component, OnInit } from '@angular/core';
import * as L from 'leaflet';
import 'leaflet.heat/dist/leaflet-heat.js';
import { RegionalService } from '../regional/regional.service';
@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss'],
})
export class MapComponent implements OnInit {
  addressPoints = [];
  // options = {
  //   layers: [
  //     L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
  //       maxZoom: 18,
  //       attribution: '',
  //     }),
  //   ],
  //   zoom: 5,
  //   center: L.latLng(21.0278, 105.8342),
  //   worldCopyJump: true,
  // };
  options = {
    layers: [
      L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        maxZoom: 18,
        attribution: '...',
      }),
    ],
    zoom: 5,
    center: L.latLng(21.0278, 105.8342),
    worldCopyJump: true,
  };
  layersControl = {
    baseLayers: {
      'Open Street Map': L.tileLayer(
        'http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
        { maxZoom: 18, attribution: '...' }
      ),
      'Open Cycle Map': L.tileLayer(
        'http://{s}.tile.opencyclemap.org/cycle/{z}/{x}/{y}.png',
        { maxZoom: 18, attribution: '...' }
      ),
    },
    // overlays: {
    //   'Big Circle': L.circle([46.95, -122], { radius: 5000 }),
    //   'Big Square': L.polygon([
    //     [46.8, -121.55],
    //     [46.9, -121.55],
    //     [46.9, -121.7],
    //     [46.8, -121.7],
    //   ]),
    // },
  };
  layers = [];
  confirmed;
  constructor(
    private regionService: RegionalService,
    private wwService: WorldwideService
  ) {}

  ngOnInit(): void {
    this.wwService.getWorldWideData().subscribe((data) => {
      this.confirmed = data.confirmed;
    });
    this.regionService.getData().subscribe((data) => {
      data.forEach((country) => {
        this.layers.push(
          L.circle([country.location.lat, country.location.lng], {
            radius: 200000,
            color: 'red',
            opacity: 0.5,
          })
        );
        // this.addressPoints.push([
        //   country.location.lat,
        //   country.location.lng,
        //   country.confirmed,
        // ]);
      });
    });
  }
  onMapReady(map): void {
    // const heat = (L as any).heatLayer(this.addressPoints).addTo(map);
  }
}
