import { WorldwideService } from './worldwide.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-worldwide',
  templateUrl: './worldwide.component.html',
  styleUrls: ['./worldwide.component.scss'],
})
export class WorldwideComponent implements OnInit {
  constructor(private wwService: WorldwideService) {}
  wwData = { rate: 0, recovered: 0, deaths: 0, confirmed: 0 };
  ngOnInit(): void {
    this.wwService.getWorldWideData().subscribe((data) => {
      this.wwData = { ...data };
      this.wwData['rate'] = (this.wwData.deaths / this.wwData.confirmed) * 100;
    });
  }
}
